import {mapState} from "vuex";

export default {
    name: 'app-nav-bar',
    data() {
        return {
            hw_style: {},
        }
    },
    watch: {
        'leftIcon'(newValue, oldValue) {
            console.log('lefticon 监听测试');
            this.doSomething();
        }
    },
    props: {
        fixed: {
            type: [Boolean, String],
            default: true
        },
        shadow: {
            type: [String, Boolean],
            default: false
        },
        border: {
            type: [String, Boolean],
            default: false
        },
        backgroundColor: {
            type: String,
            default: "#FFFFFF"
        },
        leftIcon: {
            type: String,
            default: ""
        },
        link: {
            type: [Object,Array]
        },
        title: {
            type: String,
            default: ""
        },
        xStyle: {
            type: [Number, String],
            default: 1
        },
        hasMallSetting: {
            type: [Number, String],
            default: 1
        },
        color: {
            type: String,
            default: "#000000"
        },
        position: {
            type: String,
            default: "center"
        },
        placeholder: {
            type: String,
            default: "搜索"
        },
        placeholderColor: {
            type: String,
            default: "#666666"
        }
    },
    computed: {
        ...mapState({
            statusBarHeight: state => state.gConfig.systemInfo.statusBarHeight,
            mBarHeight: state => state.gConfig.mBarHeight,
            appImg: state => state.mallConfig.__wxapp_img.mall,
            mallNavbar: state => state.mallConfig.navbar
        }),
        maxWidth() {
            return () => {
                let xstyle = parseInt(this.xStyle);
                let width = 0;

                switch (xstyle) {
                    case 1:
                        if (this.position === 'center') {
                            width = uni.upx2px(360);
                        } else {
                            width = uni.upx2px(500);
                        }
                        break;
                    case 2:
                        width = uni.upx2px(400);
                        break;
                    case 4:
                        width = uni.upx2px(200);
                        break;
                    default:
                        break;
                }
                if (width && this.pagesLength > 1) {
                    width -= uni.upx2px(42);
                }
                return Object.assign({}, {
                    'max-width': width + 'px',
                });
            }
        },
        hiddenHeight() {
            return () => {
                let barHeight;

                barHeight = this.statusBarHeight;

                barHeight = barHeight || 0;
                return {
                    height: (barHeight + this.mBarHeight) + 'px',
                }
            }
        },
        ordinaryStyle() {
            return () => {
                let color = '';
                let backgroundColor = '';
                if (this.hasMallSetting == 1) {
                    color = this.mallNavbar.top_text_color;
                    backgroundColor = this.mallNavbar.top_background_color;
                } else {
                    color = this.color;
                    backgroundColor = this.backgroundColor;
                }
                let barHeight;

                barHeight = this.statusBarHeight;

                barHeight = barHeight || 0;
                color = color || '#000000';
                backgroundColor = backgroundColor || '#FFFFFF';
                return Object.assign({}, {
                    color, backgroundColor,
                    height: (barHeight + this.mBarHeight) + 'px',
                    paddingTop: barHeight + 'px',
                })
            }
        },
        hasJump() {
            return [2, 4].indexOf(parseInt(this.xStyle)) !== -1
        },
        showLeftIcon() {
            return [2, 3].indexOf(parseInt(this.xStyle)) !== -1 && this.leftIcon
        },
        showTitle() {
            return [1, 2, 4].indexOf(parseInt(this.xStyle)) !== -1 && this.title
        },
        showLink() {
            return [3, 4].indexOf(parseInt(this.xStyle)) !== -1 && this.link
        },
        pagesLength() {
            return getCurrentPages().length;
        },
    },
    mounted() {
        this.doSomething();
    },
    methods: {
        doSomething() {
            const maxHeight = 54;
            const maxWidth = 100;
            const self = this;
            uni.getImageInfo({
                src: self.leftIcon,
                success: function (res) {
                    let {height, width} = res;
                    if (height <= maxHeight && width <= maxWidth) {
                        // height = height;
                        // width = width;
                    }
                    if (height <= maxHeight && width >= maxWidth) {
                        height = height / (width / maxWidth);
                        width = maxWidth;
                    }
                    if (height >= maxHeight && width <= maxWidth) {
                        height = maxHeight;
                        width = width / (height / maxHeight);
                    }

                    if (height > maxHeight && width >= maxWidth) {
                        if (maxWidth / maxHeight > width / height) {
                            width = width / (height / maxHeight);
                            height = maxHeight;
                        } else {
                            height = height / (width / maxWidth);
                            width = maxWidth;
                        }
                    }
                    self.hw_style = {
                        height: uni.upx2px(height) + 'px',
                        width: uni.upx2px(width) + 'px',
                    };
                    console.log(self.hw_style);
                }
            })
        },
        navGoodsSearch() {
            uni.navigateTo({
                url: '/pages/search/search',
            })
        },
        onClickBack() {
            uni.navigateBack({
                delta: 1
            })
        },
    },
}
